<template>
  <n-space style="margin-top: 10px">
    <n-input placeholder="输入委托人员姓名" v-model:value="search.name" size="small">
      <template #prefix>
        <n-icon >
          <SearchCircleOutline />
        </n-icon>
      </template>
    </n-input>

    <n-input placeholder="输入联系电话" v-model:value="search.phone" size="small">
      <template #prefix>
        <n-icon >
          <SearchCircleOutline />
        </n-icon>
      </template>
    </n-input>
    <n-input placeholder="输入委托地址" v-model:value="search.address" size="small">
      <template #prefix>
        <n-icon >
          <SearchCircleOutline />
        </n-icon>
      </template>
    </n-input>




    <n-button type="info" ghost   @click="getData"  size="small"><n-icon size="20">
      <SearchCircleOutline />
    </n-icon >搜索</n-button>
    <n-button type="warning"  ghost  @click="clearSearch" size="small"><n-icon size="20">
      <TrashOutline />
    </n-icon >清空选择条件</n-button>
    <n-button type="warning"  disabled   size="small"><n-icon size="20">
      <TrashOutline />
    </n-icon >删除选中的数据</n-button>


  </n-space>
  <n-data-table :columns="columns" :data="data" style="margin-top: 20px" />
  <div style="display: flex;flex-direction: row-reverse;margin-top: 20px">
    <n-pagination :item-count="itemCount" :page-sizes="[5, 10, 15]" showSizePicker :on-update:page="onChangePage"
                  :on-update:page-size="onChangePageSize"
    />
  </div>
</template>

<script>

//表格属性
import {h, onMounted, reactive, ref} from "vue";
import {NButton, NText, NDatePicker, useDialog, useMessage, NTime, NAvatar} from "naive-ui";
import {getRequest, putRequest} from "../../api/request"
import {useStore} from "vuex"
import {PersonAddOutline, SearchCircleOutline, TrashOutline} from "@vicons/ionicons5";
// import {SearchCircleOutline,PersonAddOutline,TrashOutline} from '@vicons/ionicons5'

const createColumns=({accept})=>{
  return[
    {
      title:'预约行程id',
      key:'actions',
      render(row){
        return h(
            NText,
            {
            },
            {default:()=>row.id}
        )
      }
    },
    {
      title:'委托人名称',
      key:'actions',
      render(row){
        return h(
            NText,
            {
            },
            {default:()=>row.user.name}
        )
      }
    },
    {
      title:'委托人称呼',
      key:'actions',
      render(row){
        return h(
            NText,
            {
            },
            {default:()=>row.rentaldemand.call}
        )
      }
    },
    {
      title:'头像',
      key:'actions',
      render(row){
        return h(
            NAvatar,
            {
              src:row.user.userface
            },
            {}
        )
      }
    },
    {
      title: '性别',
      key: 'actions',
      render(row){
        return h(
            NText,{

            },
            {
              default:()=>row.user.gender
            }
        )
      }
    },
    {
      title: '委托地址',
      key: 'actions',
      render(row){
        return h(
            NText,
            {

            },
            {
              default:()=>row.rentaldemand.location+row.rentaldemand.community+row.rentaldemand.building+"栋"+row.rentaldemand.door+"房"
            }
        )
      }
    },
    {
      title: '联系电话',
      key: 'actions',
      render(row){
        return h(
            NText,{

            },
        {
          default:()=>row.rentaldemand.demandPhone
        }
        )
      }

    },
    {
      title: '预约时间',
      key: 'actions',
      render(row){
        return h(
            NDatePicker,{

              // defaultValue:row.appointmentDate
              value:row.appointmentDate,
              type:'datetime'}
        )
      }
    },
    {
      title: '接受此委托',
      key: 'actions',
      render(row){
        return h(
            NButton,{
              size:'tiny',
              type:'primary',
              onClick:()=>accept(row)

            },
            {
              default:()=>"接受"
            }
        )
      }

    }
  ]
}
export default {
  name: "wait",
  components:{
    SearchCircleOutline,PersonAddOutline,TrashOutline
  },
  setup(){
    const  data=ref([])
    const  dialog=useDialog()
    const   message=useMessage()
    const  store=useStore()
    const itemCount=ref()
    const search=reactive({name:'',phone:''})
    const pagination=reactive({
      page:1,
      pageSize:5,
    })


    // 该页面的数据
    function onChangePage(page){
      pagination.page=page
      getData();
      // message.success(page)
    }
    function onChangePageSize(pageSize){
      pagination.pageSize=pageSize
      getData();
      // message.success(pageSize)
    }
    //clear
    function clearSearch(){
      search.name='';
      search.phone=''
    }

    function getData() {
      getRequest('/appointment/getAllStatus0/?page='+pagination.page+"&&size="+pagination.pageSize).then(resp=>{
        data.value=resp.data.data;
        itemCount.value=resp.data.total

        console.log(resp.data)
      })


    }


    onMounted(getData)
    return{
      clearSearch,
      itemCount,
      onChangePageSize,
      onChangePage,
      search,
      getData,
      dialog,
      data,

      columns:createColumns({
        accept(row){
          dialog.warning({
            title:'警告',
            content:'你确定?',
            positiveText:'确定',
            negativeText:'取消',
            onNegativeClick:()=>{
              message.success("取消")

            },
            onPositiveClick:()=>{
              console.log(store.state.currentPeople.id)

                putRequest('/appointment/updateAdminId/?adminId='+store.state.currentPeople.id+"&&id="+row.id+"&&uid="+row.user.id).then(resp=>{
                  if (resp){
                    message.success("成功添加！");
                    getData()
                  }else {
                    message.error("添加失败！")
                  }
                })



            }

          })
        }

      })

    }
  }
}
</script>

<style scoped>

</style>
